import React, { Component, Suspense } from 'react';
import { connect } from 'react-redux';
import { themeByWallID, loadSiteFeed } from './actions/themeActions'
import { getWallID } from './utils'
import { addfeedload } from './actions/api'
const WidgetApp = React.lazy(() => import('./widgetApp'));
class Main extends Component {
    constructor(props) {
        super(props);
        let wallID = getWallID()
        if (wallID && String(wallID).length > 0 && String(wallID)[0] == "0") wallID = String(wallID).slice(1)
        this.state = {
            wall_id: wallID
        }
        if (addfeedload) {
            loadSiteFeed()
        }
        else props.themeByWallID(wallID, null)

    }
    render() {
        const { wall_id } = this.state;
        return <Suspense fallback={<></>}> <WidgetApp wallID={wall_id} /></Suspense>
    }
}

const mapStateToProps = state => {
    return {}
}
const mapDispatchToProps = dispatch => {
    return {
        themeByWallID: (Id, heightEvent) => dispatch(themeByWallID(Id, heightEvent))
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(Main);